import React from "react";
import { Dots } from 'react-activity';

export default class FourthStep extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            isLoading:false
        }
    }

    onPreviousStepPress = () => {
        window.scrollTo(0,0);
        this.props.previousStep();
    }

    onNextStepPress = () => {
        window.scrollTo(0,0);
        this.props.nextStep();
    }

    render () {
        return (
        <div className="step">
            <h2 className="step-title">Payment</h2>
            {/* <div className="alert alert-info" role="alert">
            You are about to pay an amount of <b>250€</b>
            </div> */}
            <div className="contained">
                <div className="row">
                    <div className="col-md-6 mb-3">
                        <label>Name on card</label>
                        <input type="text" className="form-control" id="cc-name" placeholder="Mr DOE John"/>
                        <small className="text-muted">Full name as displayed on card</small>
                        <div className="invalid-feedback">
                        Name on card is required
                        </div>
                    </div>
                    <div className="col-md-6 mb-3">
                        <label >Credit card number</label>
                        <input type="text" className="form-control" id="cc-number" placeholder="5676XXXXXXXXX"/>
                        <div className="invalid-feedback">
                        Credit card number is required
                        </div>
                    </div>
                </div>
                <div className="row">
                <div className="col-md-3 mb-3">
                    <label>Expiration</label>
                    <input type="text" className="form-control" id="cc-expiration" placeholder="MM/AA"/>
                    <div className="invalid-feedback">
                    Expiration date required
                    </div>
                </div>
                <div className="col-md-3 mb-3">
                    <label >CVV</label>
                    <input type="text" className="form-control" id="cc-cvv" placeholder="XXX"/>
                    <div className="invalid-feedback">
                    Security code required
                    </div>
                </div>
                </div>
            </div>
            <div className="form-group nav-line">
                {/* <button className="btn btn-secondary" onClick={this.onPreviousStepPress}>Previous</button> */}
                <button style={{width:"100%"}}className="btn btn-primary" onClick={this.onNextStepPress}>{this.state.isLoading ? <Dots color="white" /> : "Pay"}</button>
            </div>
        </div>
        )
    }
    
}